.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px;
  display: flex;
}

.card__picture {
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews-text {
  font-size: 10px;
  line-height: 14px;
}

.card__product-price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__product-price_title {
  color: #616070;
  font-size: 12px;
}

.card__product-price_total {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}

.stars {
  justify-content: left;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #e4e4e4;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}
